import React from 'react'
import * as filestack from 'filestack-js'
import styles from './styles/admin-dashboard.module.css'
import { useState } from 'react'
import axios from 'axios'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { Spinner } from '../../utils/Spinner'

const Index = ({ token, showMessage, hideMessage }) => {
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState(false)
    const [productName, setProductName] = useState('')
    const [category, setCategory] = useState(' ')
    const [price, setPrice] = useState('')
    const [currency, setCurrency] = useState('')
    const [description, setDescription] = useState('')
    const [productPicture, setProductPicture] = useState("")
    const [extraImage1, setExtraImage1] = useState("")
    const [extraImage2, setExtraImage2] = useState("")
    const [extraImage3, setExtraImage3] = useState("")
    const [extraImage4, setExtraImage4] = useState("")

    const FD = new FormData()
    FD.append('productName', productName)
    FD.append('categoryName', category)
    FD.append('productPrice', price)
    FD.append('currency', currency)
    FD.append('productDescription', description)
    FD.append('productPicture', productPicture)
    FD.append('extraImage1', extraImage1)
    FD.append('extraImage2', extraImage2)
    FD.append('extraImage3', extraImage3)
    FD.append('extraImage4', extraImage4)
    const modalLinksSet1 = [' ','',' ',' ','','',' ',' ']
    const modalLinksSet2 = [' ','  ','','  ',' ','',' ',' ']
    const modalLinksSet3 = ['  ',' ',' ',' ','  ','Gaming']
    const menuLinksSet = [...modalLinksSet1,...modalLinksSet2,...modalLinksSet3]

    const uploadImage = (e, setState) => {
        e.preventDefault()
        const client = filestack.init('AbfKBGFGMRzeAj9M3txiKz')
        client.picker({imageMax: [350, 350], maxFiles: 1, onUploadDone: res => {
            setState(res.filesUploaded[0].url)
        }}).open()
    }

    const shipProduct = (e, form, token) => {
        e.preventDefault()
        setLoading(true)
        setError(false)
        axios.post('/product',form, {
            headers: {
                Authorization: token
            }
        }).then(res => {
            console.log(res)
            setLoading(false)
            setProductName('')
            setCategory(' ')
            setPrice('')
            setCurrency('')
            setDescription('')
            setProductPicture(null)
            showMessage()
            setTimeout(hideMessage, 3000)
            setProductPicture('')
            setExtraImage1('')
            setExtraImage2('')
            setExtraImage3('')
            setExtraImage4('')
        }).catch(e => {
            setError(true)
            setLoading(false)
        })
    }


    return (
        <div className={styles.adminDashboard} >
            { loading ? <div className={styles.loadingScreen}> <Spinner /> </div>: ''}
            <div className={styles.formSection} >
                <h1 className={styles.formHeader}> </h1>
                <form name="photos" encType="multipart/form-data" onSubmit={(e) => shipProduct(e, FD, token)}  >

                    <div className={styles.inputArea} >
                        <input type="text" value={productName} onChange={e => setProductName(() => e.target.value)} className={styles.inputField} id="name" />
                        <label htmlFor="name" className={styles.label} > </label>
                    </div>
                    
                    <div className={styles.inputArea} >
                        <select name="category" id="category" onChange={e => setCategory(e.target.value)} className={styles.inputField}>
                            {menuLinksSet.map(el => <option key={el} value={el}> {el} </option>)}
                        </select>
                        <label htmlFor="category" className={styles.label} ></label>
                    </div>
                    
                    <div className={styles.inputArea} >
                        <input type="text" value={price} onChange={e => setPrice(() => e.target.value)} className={styles.inputField} id="price" />
                        <label htmlFor="price" className={styles.label} ></label>
                    </div>

                    <div className={styles.inputArea} >
                        <select name="currency" id="currency" onChange={e => setCurrency(e.target.value)} className={styles.inputField}>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                        <label htmlFor="currency" className={styles.label} ></label>
                    </div>
                    
                    <div className={styles.inputArea} >

                        <input type="text" value={description} onChange={e => setDescription(() => e.target.value)} className={styles.inputField} id="description" />
                        <label htmlFor="description" className={styles.label} > </label>
                    </div>
                
                    <div className={styles.inputArea} >
                        <input type="submit" value=" " className={styles.imageInput} id="picture" onClick={(e) => uploadImage(e, setProductPicture)} />
                        {productPicture? <span style={{color: 'green', fontSize: '1.6rem'}} >  </span> : ''}
                        <label htmlFor="picture" className={styles.label} >  </label>
                    </div>
                    
                
                    <div className={styles.inputArea} >
                        <input type="submit" value="  1" className={styles.imageInput} id="picture1" onClick={(e) => uploadImage(e, setExtraImage1)} />
                        {extraImage1? <span style={{color: 'green', fontSize: '1.6rem'}} >  </span> : ''}
                        <label htmlFor="picture1" className={styles.label} >  1</label>
                    </div>
                    
                
                    <div className={styles.inputArea} >
                        <input type="submit" value="  2"  className={styles.imageInput} id="picture2" onClick={(e) => uploadImage(e, setExtraImage2)} />
                        {extraImage2? <span style={{color: 'green', fontSize: '1.6rem'}} >  </span> : ''}
                        <label htmlFor="picture2" className={styles.label} >  2</label>
                    </div>
                    
                
                    <div className={styles.inputArea} >
                        <input type="submit" value="  3"  className={styles.imageInput} id="picture3" onClick={(e) => uploadImage(e, setExtraImage3)} />
                        {extraImage3? <span style={{color: 'green', fontSize: '1.6rem'}} >  </span> : ''}
                        <label htmlFor="picture3" className={styles.label} >  3</label>
                    </div>
                    
                
                    <div className={styles.inputArea} >
                        <input type="submit" value="  4"  className={styles.imageInput} id="picture4" onClick={(e) => uploadImage(e, setExtraImage4)} />                        
                        {extraImage4? <span style={{color: 'green', fontSize: '1.6rem'}} >  </span> : ''}
                        <label htmlFor="picture4" className={styles.label} >  4</label>
                    </div>
                    
                    <button type="submit" className={styles.cta} >   </button>
                    { error ? <p style={{fontSize: '2rem', color: 'red',textAlign: 'right', paddingRight: '1rem'}} >   </p> : ''}
                    <Link to="/orders" className={styles.cta} style={{background: '#1fc739'}} > </Link>
                </form>
                
            </div>
        </div>
    )
}



const mapStateToProps = state => {
    return {
         token: state.adminAuth.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        showMessage: () => dispatch({type: 'SHOW_MESSAGE', payload: '  '}),
        hideMessage: () => dispatch({type: 'HIDE_MESSAGE'})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Index)